.page {
  font-family: "Cinzel", serif;
  font-size: 16px;
  line-height: 27px;
  color: $color-main;
  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body{
    margin: 0;
    padding: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

h1,
h2,
h3,
p {
  margin: 0;
}

